/* eslint-disable */
<template>
  <div>
    <BannerCarousel />
    <Shipping />
    <Sales />
    <CarouselProduct />
    <KidsComponent />
    <Products />
    <LoadingComponent v-if="loading" />
  </div>
</template>

<script>
import BannerCarousel from "../components/BannerCarousel";
import Shipping from "../components/Shipping";
import Products from "../components/Products";
import Sales from "../components/Sales";
import KidsComponent from "../components/KidsComponent";
import CarouselProduct from "../components/CarouselProduct";
import LoadingComponent from "../components/LoadingComponent";

export default {
  name: "Home",
  components: {
    BannerCarousel,
    Shipping,
    Products,
    Sales,
    KidsComponent,
    CarouselProduct,
    LoadingComponent
  },
  data() {
    return {
      loading: true
    };
  },
  methods: {
    processLoading: function() {
      this.loading = true;
      this.$store.state.showSearch = false;
      setTimeout(() => {
        this.closeLoading();
      }, 3000);
    },
    closeLoading: function() {
      if (this.loading) {
        this.loading = false;
      }
    }
  },
  mounted() {
    this.processLoading();
    
  }
};
</script>

