/* eslint-disable */
<template>
  <div>
    <!-- About us -->
    <AboutBanner />
    <AboutGrid />
    <AboutSection />
    <LoadingComponent v-if="loading" />
  </div>
</template>

<script>
import AboutBanner from "../components/AboutBanner";
import AboutGrid from "../components/AboutGrid";
import AboutSection from "../components/AboutSection";
import LoadingComponent from "../components/LoadingComponent";

export default {
  name: "About",
  components: {
    AboutBanner,
    AboutGrid,
    AboutSection,
    LoadingComponent
  },
  data() {
    return {
      loading: true
    };
  },
  methods: {
    // WHEN PAGE LOADS, SHOW LOADING
    processLoading: function() {
      setTimeout(() => {
        this.closeLoading();
      }, 3000);
    },
    closeLoading: function() {
      if (this.loading) {
        this.loading = false;
      }
    }
  },
  mounted() {
    this.processLoading();
    this.$store.state.showSearch = false;
  }
};
</script>

