/* eslint-disable */
<template>
  <!-- ############### MOST WANTED ################## -->
  <div class="container-fluid bg-grayish-white trent pt-5 pb-5">
    <h4>MOST WANTED</h4>
    <div class="underline-green"></div>
    <div class="boxes">
      <ProductCard v-for="product in products" :key="product.id" :product="product" />
    </div>
  </div>
</template>

<script>
import ProductCard from "./ProductCard";
export default {
  name: "Products",
  components: {
    ProductCard
  },

  computed: {
    products() {
      let product = this.$store.state.allProducts;
      let shuffleProduct = this.shuffleArray(product);
      let newProduct = shuffleProduct.slice(0, 12);
      return newProduct;
    }
  },
  methods: {
    shuffleArray: function(array) {
      var ctr = array.length,
        temp,
        index;
      while (ctr > 0) {
        index = Math.floor(Math.random() * ctr);
        ctr--;
        temp = array[ctr];
        array[ctr] = array[index];
        array[index] = temp;
      }
      return array;
    }
  }
};
</script>

